
'use client';

import { useEffect, useState, useTransition } from 'react';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import * as z from 'zod';
import { useRouter } from 'next/navigation';

import type { BuildingSubmissionFormData, Competition } from '@/types';
import { submitNewBuilding, getSubmittableCompetitions } from '@/app/actions';

import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Textarea } from '@/components/ui/textarea';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Form, FormControl, FormDescription, FormField, FormItem, FormLabel, FormMessage } from '@/components/ui/form';
import { useToast } from '@/hooks/use-toast';
import { Loader2, UploadCloud, ShieldAlert } from 'lucide-react';
import { Alert, AlertDescription, AlertTitle } from '@/components/ui/alert';

const BUILDING_SIZES = ['小型', '中型', '大型'] as const;

const submissionSchema = z.object({
  name: z.string().min(3, { message: '作品名称至少需要3个字符。' }).max(100, { message: '作品名称不能超过100个字符。' }),
  description: z.string().min(10, { message: '作品描述至少需要10个字符。' }).max(1000, { message: '作品描述不能超过1000个字符。' }),
  imageUrl: z.string().url({ message: '请输入有效的图片URL。以 https://placehold.co/ 开头用于占位。' }).startsWith("https://placehold.co/", {message: "测试阶段请使用 https://placehold.co/ 生成的图片URL"}),
  size: z.enum(BUILDING_SIZES, { required_error: '请选择建筑规格。' }),
  territoryIdentifier: z.string().min(3, { message: '领地标识至少3字符。' }).max(50, {message: "领地标识不超过50字符"}).regex(/^[a-zA-Z0-9_]+$/, { message: '领地标识只能包含英文、数字和下划线。' }),
  server: z.string().min(1, { message: '服务器名称不能为空。' }),
  coordinateX: z.coerce.number(),
  coordinateY: z.coerce.number(),
  coordinateZ: z.coerce.number(),
  competitionId: z.string({ required_error: '请选择一个参赛活动。' }),
  aiHint: z.string().optional().refine(val => !val || val.split(' ').length <= 2, { message: 'AI提示最多包含两个关键词，用空格分隔。'}),
});


export default function SubmitProjectPage() {
  const [isSubmitting, startSubmitTransition] = useTransition();
  const { toast } = useToast();
  const router = useRouter();
  const [competitions, setCompetitions] = useState<Competition[]>([]);
  const [loadingCompetitions, setLoadingCompetitions] = useState(true);

  const form = useForm<BuildingSubmissionFormData>({
    resolver: zodResolver(submissionSchema),
    defaultValues: {
      name: '',
      description: '',
      imageUrl: 'https://placehold.co/800x600.png',
      size: '中型',
      territoryIdentifier: '',
      server: 'Survival-1',
      coordinateX: 0,
      coordinateY: 64,
      coordinateZ: 0,
      competitionId: undefined,
      aiHint: '',
    },
  });

  useEffect(() => {
    async function fetchCompetitions() {
      try {
        setLoadingCompetitions(true);
        const activeComps = await getSubmittableCompetitions();
        setCompetitions(activeComps);
      } catch (error) {
        console.error("Failed to fetch competitions:", error);
        toast({ title: "错误", description: "加载比赛列表失败。", variant: "destructive" });
      } finally {
        setLoadingCompetitions(false);
      }
    }
    fetchCompetitions();
  }, [toast]);

  const onSubmit = async (values: BuildingSubmissionFormData) => {
    startSubmitTransition(async () => {
      const result = await submitNewBuilding(values);
      if (result.success) {
        toast({
          title: '提交成功！',
          description: result.message,
        });
        form.reset(); // Reset form
        router.push('/dashboard/my-projects'); // Navigate to my projects page
      } else {
        toast({
          title: '提交失败',
          description: result.message || '发生未知错误。',
          variant: 'destructive',
        });
      }
    });
  };

  return (
    <Card className="shadow-xl">
      <CardHeader>
        <CardTitle className="text-2xl flex items-center">
          <UploadCloud className="mr-3 h-7 w-7 text-primary" />
          提交新的建筑作品
        </CardTitle>
        <CardDescription>请填写以下信息来报名参加活动。作品提交后状态默认为“等待审核”。</CardDescription>
      </CardHeader>
      <CardContent>
        <Form {...form}>
          <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-6">
            <FormField
              control={form.control}
              name="name"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>作品名称</FormLabel>
                  <FormControl><Input placeholder="例如：天空之城" {...field} /></FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />

            <FormField
              control={form.control}
              name="description"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>作品描述</FormLabel>
                  <FormControl><Textarea placeholder="详细描述您的作品特点、灵感来源等..." {...field} rows={4} /></FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />

            <FormField
              control={form.control}
              name="imageUrl"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>作品图片URL</FormLabel>
                  <FormControl><Input placeholder="https://placehold.co/800x600.png" {...field} /></FormControl>
                  <FormDescription>请提供一个公开可访问的图片链接。测试阶段请使用 placehold.co 生成的链接。</FormDescription>
                  <FormMessage />
                </FormItem>
              )}
            />
             <FormField
              control={form.control}
              name="aiHint"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>图片AI提示词 (可选)</FormLabel>
                  <FormControl><Input placeholder="例如: castle sky" {...field} /></FormControl>
                  <FormDescription>最多两个英文关键词，用于未来可能的图片优化或搜索，例如 "minecraft castle"。用空格分隔。</FormDescription>
                  <FormMessage />
                </FormItem>
              )}
            />

            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <FormField
                control={form.control}
                name="size"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>建筑规格</FormLabel>
                    <Select onValueChange={field.onChange} defaultValue={field.value}>
                      <FormControl><SelectTrigger><SelectValue placeholder="选择规格" /></SelectTrigger></FormControl>
                      <SelectContent>
                        {BUILDING_SIZES.map(s => <SelectItem key={s} value={s}>{s}</SelectItem>)}
                      </SelectContent>
                    </Select>
                    <FormMessage />
                  </FormItem>
                )}
              />
              <FormField
                control={form.control}
                name="competitionId"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>选择参赛活动</FormLabel>
                    <Select onValueChange={field.onChange} defaultValue={field.value} disabled={loadingCompetitions || competitions.length === 0}>
                      <FormControl><SelectTrigger>
                        <SelectValue placeholder={loadingCompetitions ? "加载活动中..." : (competitions.length === 0 ? "暂无可报名活动" : "选择一个活动")} />
                      </SelectTrigger></FormControl>
                      <SelectContent>
                        {competitions.map(comp => <SelectItem key={comp.id} value={comp.id}>{comp.name} ({comp.year})</SelectItem>)}
                      </SelectContent>
                    </Select>
                    {competitions.length === 0 && !loadingCompetitions && <FormDescription className="text-red-500">当前没有可报名的活动。</FormDescription>}
                    <FormMessage />
                  </FormItem>
                )}
              />
            </div>

            <Card>
              <CardHeader className="pb-2">
                <CardTitle className="text-lg">领地信息</CardTitle>
                <CardDescription>请提供作品在游戏内的领地详情。</CardDescription>
              </CardHeader>
              <CardContent className="space-y-4 pt-2">
                <FormField
                  control={form.control}
                  name="territoryIdentifier"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>领地标识 (英文名)</FormLabel>
                      <FormControl><Input placeholder="例如：MyAwesomeCastle" {...field} /></FormControl>
                      <FormDescription>对应 `/res create [年份前缀]领地标识` 中的英文部分。</FormDescription>
                      <FormMessage />
                    </FormItem>
                  )}
                />
                <FormField
                  control={form.control}
                  name="server"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>所在服务器</FormLabel>
                      <FormControl><Input placeholder="例如：Survival-1" {...field} /></FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />
                <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                  <FormField
                    control={form.control}
                    name="coordinateX"
                    render={({ field }) => (
                      <FormItem>
                        <FormLabel>坐标 X</FormLabel>
                        <FormControl><Input type="number" placeholder="0" {...field} /></FormControl>
                        <FormMessage />
                      </FormItem>
                    )}
                  />
                  <FormField
                    control={form.control}
                    name="coordinateY"
                    render={({ field }) => (
                      <FormItem>
                        <FormLabel>坐标 Y</FormLabel>
                        <FormControl><Input type="number" placeholder="64" {...field} /></FormControl>
                        <FormMessage />
                      </FormItem>
                    )}
                  />
                  <FormField
                    control={form.control}
                    name="coordinateZ"
                    render={({ field }) => (
                      <FormItem>
                        <FormLabel>坐标 Z</FormLabel>
                        <FormControl><Input type="number" placeholder="0" {...field} /></FormControl>
                        <FormMessage />
                      </FormItem>
                    )}
                  />
                </div>
              </CardContent>
            </Card>
            
            <Alert variant="default" className="bg-primary/10 border-primary/30">
              <ShieldAlert className="h-4 w-4 text-primary" />
              <AlertTitle className="text-primary/90">提交流程提醒</AlertTitle>
              <AlertDescription className="text-primary/80">
                提交作品即表示您同意遵守活动规则。作品提交后将进入“等待审核”状态，管理员审核通过后方可参与投票和展示。
                请确保所有信息填写准确，特别是领地信息。
              </AlertDescription>
            </Alert>

            <Button type="submit" className="w-full md:w-auto" disabled={isSubmitting || loadingCompetitions}>
              {isSubmitting ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : <UploadCloud className="mr-2 h-4 w-4" />}
              {isSubmitting ? '正在提交...' : '确认提交作品'}
            </Button>
          </form>
        </Form>
      </CardContent>
    </Card>
  );
}

